import React, { Component } from 'react';
import { HashRouter } from 'react-router-dom';
import Footer from './Footer';
import Header from './Header';
import Logo from './Logo';
import Main from './Main';
import Navi from './Navi';
import './Page.css';

class Page extends Component {
  render() {
    return (
      <HashRouter>
        <div className="page">
          <Logo />
          <Header />
          <div className="spacer"></div>
          <Navi contentToShow={this.props.contentToShow}
                showAbout={this.props.showAbout}
                showKnowledge={this.props.showKnowledge}
                showPortfolio={this.props.showPortfolio}
                showBlogs={this.props.showBlogs}
                showContact={this.props.showContact} />
          <Main />
          <Footer />
        </div>
      </HashRouter>
    );
  }
}

export default Page;
